/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
html {
  font-family: monoOne, monospace;
}
/* Primary Font */
@font-face {
  font-family: 'monoOne';
  src: url('../fonts/monoOne.otf') format('otf'), url('../fonts/monoOne.woff') format('woff');
}
.isHidden {
  display: none !important;
}
.u-float-right {
  float: right;
}
.u-center-middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.footer {
  height: 36px;
  width: 100%;
  background-color: #141414;
  display: flex;
  justify-content: space-between;
}
.footer > * {
  box-sizing: border-box;
}
.stripView {
  width: 400px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 36px;
  margin: 0 auto;
  font-family: Monospace, serif;
  font-size: 12px;
  max-width: 500px;
  background-color: rgba(20, 20, 20, 0.6);
  box-sizing: border-box;
  transition: all ease-in-out 0.3s;
  z-index: 100;
}
.mix-stripView_isHidden {
  right: -390px;
}
.stripTitle {
  background-color: #222222;
  color: #ffffff;
  font-size: 1.25em;
  padding: 5px 0;
  text-align: center;
}
.stripView-list {
  width: calc(100% -  10px );
  overflow-y: scroll;
  list-style-type: none;
  height: calc(50% - 1.25em - 10px);
  display: flex;
  flex-direction: column;
}
.stripViewItem {
  border: 1px solid #222;
  transition: ease-in-out margin 0.15s;
}
.stripViewItem:first-child {
  margin-top: auto;
}
.active,
.stripViewItem:hover {
  margin-left: 5px;
  cursor: pointer;
}
.stripView-list > * + * {
  margin-top: 3px;
  border-top: none;
}
.stripViewItem-table {
  width: 100%;
  background-color: #ffffff;
  border-collapse: collapse;
  box-sizing: border-box;
}
.stripView-table-td {
  padding: 3px 5px;
  text-align: center;
  overflow: hidden;
  vertical-align: top;
  box-sizing: border-box;
}
.stripView-table-td + .stripView-table-td {
  border-left: 1px solid;
}
.stripView-table-td > span {
  float: right;
}
.stripView-table-tr > .stripView-table-td:nth-child(0n+1) {
  width: 70px;
  text-align: left;
}
.stripView-table-tr > .stripView-table-td:nth-child(0n+2),
.stripView-table-tr > .stripView-table-td:nth-child(0n+3) {
  width: 50px;
}
.stripView-table-tr > .stripView-table-td:nth-child(0n+4) {
  text-align: left;
}
.stripView-table-tr > .stripView-table-td:nth-child(0n+5) {
  width: 50px;
  text-align: right;
  border: none;
}
.stripView-preplanning {
  color: red;
}
.mix-stripView-table_borderTop {
  border-top: 1px solid;
}
.stripView-trigger {
  height: 100%;
  width: 10px;
  float: left;
  background-color: rgba(20, 20, 20, 0.6);
  cursor: pointer;
}
.stripView-trigger:after {
  position: absolute;
  top: 40%;
  left: -25px;
  padding: 20px 6px;
  color: #ffffff;
  font-weight: 700;
  content: ">|";
  border-radius: 8px 0 0 8px;
}
.mix-stripView_isHidden .stripView-trigger:after {
  border-radius: 0 8px 8px 0;
  transform: rotateY(-180deg);
}
.mix-stripView_isHidden .stripView-trigger,
.mix-stripView_isHidden .stripView-trigger:after {
  background-color: #141414;
}
.mix-stripView_isHidden .stripView-trigger:hover,
.mix-stripView_isHidden .stripView-trigger:hover:after {
  background-color: rgba(20, 20, 20, 0.6);
}
.stripView-trigger {
  background-color: transparent;
}
.stripView-trigger:after {
  background-color: rgba(20, 20, 20, 0.6);
}
.stripView-trigger:hover,
.stripView-trigger:hover:after {
  background-color: #141414;
}
.departure {
  border-left: 5px solid rgba(128, 255, 255, 0.8);
}
.arrival {
  border-left: 5px solid rgba(224, 128, 128, 0.8);
}
.overflight {
  border-left: 5px solid rgba(219, 204, 0, 0.8);
}
.nice-scrollbar::-webkit-scrollbar {
  width: 8px;
  padding: 4px;
}
.nice-scrollbar::-webkit-scrollbar-thumb {
  width: 4px;
  background-color: rgba(255, 255, 255, 0.4);
}
.nice-scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.6);
}
.nice-scrollbar::-webkit-scrollbar-thumb:active {
  background-color: rgba(255, 255, 255, 0.5);
}
#tutorial {
  min-height: 72px;
  height: auto;
  width: 480px;
  padding: 12px 15px;
  position: absolute;
  background-color: #141414;
  font-size: 12px;
  color: #dddddd;
  border-radius: 1px;
  opacity: 0;
  transition: opacity 0.5s ease;
  box-sizing: border-box;
  box-shadow: 0 0 0 6px #28505a;
  pointer-events: none;
  z-index: 200;
}
#tutorial.open {
  opacity: 1;
  pointer-events: auto;
}
#tutorial h1 {
  padding-bottom: 10px;
  font-size: 120%;
}
#tutorial main {
  line-height: 125%;
}
#tutorial .prev,
#tutorial .next {
  height: 100%;
  width: 24px;
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
#tutorial .prev img,
#tutorial .next img {
  position: absolute;
  top: 50%;
  margin-top: -24px;
  opacity: 0.5;
}
#tutorial .prev:hover,
#tutorial .next:hover {
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
#tutorial .prev:active,
#tutorial .next:active {
  background-color: rgba(0, 0, 0, 0.6);
}
#tutorial .prev:hover img,
#tutorial .next:hover img {
  opacity: 0.8;
}
#tutorial .prev:active img,
#tutorial .next:active img {
  opacity: 0.6;
}
#tutorial .prev {
  left: 0;
  margin-left: -30px;
}
#tutorial .next {
  right: 0;
  margin-right: -30px;
}
#tutorial ul {
  margin: 0.5em 2em;
}
#tutorial a {
  color: cornflowerblue;
}
.airport-list-item {
  margin-bottom: 2px;
  padding: 8px 5px;
  list-style-type: none;
  font-weight: bold;
}
.airport-list-item .difficulty {
  width: 5em;
  display: inline-block;
  font-size: 120%;
  text-align: right;
  white-space: nowrap;
}
.airport-list-item .icao {
  width: 2.5em;
  margin-left: 1em;
  display: inline-block;
  text-align: center;
}
.airport-list-item .name {
  margin-left: 1em;
  display: inline-block;
  text-align: center;
}
.airport-list-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
.airport-list-item.active:hover {
  cursor: auto;
}
.mix-airport-list-item_isActive {
  margin-left: 0;
  background-color: #28505a;
  cursor: pointer;
}
#airportGuide-container {
  background-color: rgba(20, 20, 20, 0.85);
  border: 6px solid #28505a;
  border-radius: 8px;
  box-shadow: 0 0 50px -10px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  height: auto;
  margin: auto;
  max-height: calc(85% - 2.3em - 20px);
  max-width: 80%;
  position: absolute;
  transition: opacity 0.5s ease;
  left: -99999px;
  opacity: 0;
  overflow-y: auto;
  pointer-events: none;
  width: fit-content;
  z-index: 200;
}
#airportGuide-container.open {
  bottom: 0;
  left: 0;
  opacity: 1;
  pointer-events: auto;
  right: 0;
  top: 0;
}
#airportGuide-bd {
  color: #dddddd;
  font-size: 12px;
  padding: 10px 15px;
}
/*
    markup here is driven by markdown files converted to html.
    we cannot control classnames any deeper than the container
    so everything must be styled via attributes.
 */
#airportGuide-bd h1,
#airportGuide-bd h2,
#airportGuide-bd h3,
#airportGuide-bd h4,
#airportGuide-bd h5,
#airportGuide-bd h6 {
  font-size: 16px;
  padding-top: 15px;
}
#airportGuide-bd p {
  padding: 15px 0;
}
#airportGuide-bd p + p {
  padding: 5px 0;
}
#airportGuide-bd a {
  color: #dddddd;
}
#airportGuide-bd table,
#airportGuide-bd th,
#airportGuide-bd td {
  border: 1px solid #666666;
  border-collapse: collapse;
}
#airportGuide-bd th {
  padding: 5px;
}
#airportGuide-bd td {
  padding: 3px;
}
/*
    These are designed for use with "black-boxed" content - that is,
    we generate the HTML from something else (i.e. markdown), so we
    shouldn't modify it.

    Because the HTML is generated from markdown, we can't assign classes
    to it directly, so we have to use element selectors.
*/
.blackBox h1,
.blackBox h2,
.blackBox h3,
.blackBox h4,
.blackBox h5,
.blackBox h6 {
  font-size: 16px;
  padding: 30px 0 10px 0;
}
.blackBox li {
  padding: 5px 0 0 0;
}
.blackBox a {
  color: #dddddd;
}
.blackBox table,
.blackBox th,
.blackBox td {
  border: 1px solid #666666;
  border-collapse: collapse;
}
.blackBox th {
  padding: 5px;
}
.blackBox td {
  padding: 3px;
}
canvas {
  position: absolute;
  top: 0;
  left: 0;
}
#canvases {
  width: 100%;
  height: calc(100% -  36px );
  overflow: hidden;
  position: relative;
}
.paused #canvases {
  opacity: 0.5;
}
#static-canvas {
  z-index: 1;
}
#dynamic-canvas {
  z-index: 2;
}
.canvas-theme-default {
  background-color: #102125;
}
.canvas-theme-classic {
  background-color: #335544;
}
.changelog-container {
  width: fit-content;
  max-width: 70%;
  height: fit-content;
  max-height: calc(100% - 200px);
  overflow-y: scroll;
  padding: 15px;
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #141414;
  color: #dddddd;
  font-size: 12px;
  font-family: inherit;
  opacity: 0;
  border-radius: 1px;
  box-sizing: border-box;
  box-shadow: 0 0 0 6px #28505a;
  pointer-events: none;
  transition: opacity 0.5s ease;
  z-index: 200;
}
.changelog-container.open {
  pointer-events: auto;
  opacity: 1;
}
.changelog-hd {
  padding-bottom: 5px;
  font-size: 2em;
  text-align: center;
  box-sizing: border-box;
}
.changelog-version {
  padding: 5px;
  font-size: 1em;
  text-align: center;
  text-decoration: underline;
  box-sizing: border-box;
}
.changelog-view-full {
  padding-top: 10px;
  font-size: 0.7em;
  text-align: center;
  color: #dddddd;
  box-sizing: border-box;
}
.changelog-view-full a {
  color: inherit;
}
#command,
#command:active,
#command:focus {
  height: 100%;
  padding: 0 18px;
  flex-grow: 1;
  background-color: transparent;
  font-size: 100%;
  font-family: inherit;
  color: #dddddd;
  user-select: text;
  border: none;
  overflow: hidden;
  outline: none;
}
.controls-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: 125;
}
.control-item {
  float: left;
  display: inline-block;
}
.control-item:hover .control-icon,
.control-icon:hover {
  box-shadow: inset 0 4px 0 #333333;
}
.control-item:hover .control-icon img,
.control-icon:hover img {
  opacity: 0.9;
  cursor: pointer;
}
.control-item:hover .control-item-content {
  display: block;
}
.control-icon {
  display: inline-block;
  text-align: center;
  text-decoration: none;
}
.control-icon img {
  opacity: 0.5;
  transition: opacity 0.1s ease;
}
.control-icon:active img {
  opacity: 0.6;
}
.control-icon.active {
  margin-left: 0;
}
.control-item-content {
  display: none;
  position: absolute;
  background-color: #141414;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  bottom: 36px;
}
.control-item-content a {
  color: #aaaaaa;
  text-decoration: none;
  display: block;
  text-align: left;
  cursor: pointer;
  clear: both;
  padding-right: calc(36px / 4);
  /*disable text selection*/
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
  border-bottom-color: #555555;
  border-bottom-width: 1px;
  border-bottom-style: inset;
}
.control-item-content a img {
  float: left;
}
.control-item-content a span {
  line-height: 36px;
}
.control-item-content a:hover,
.control-item-content a.active:hover {
  background-color: #555555;
}
.control-item-content a.active {
  color: #dddddd;
  margin: 0;
  background-color: #333333;
  box-shadow: inset 10px 0 1px -6px #386f7d;
}
.control-icon.speed-2,
.active {
  box-shadow: inset 0 4px 0 #386f7d;
}
.control-icon.speed-5,
.warning-button {
  box-shadow: inset 0 4px 0 #ff7766;
}
.control-icon.speed-5:hover,
.warning-button:hover {
  box-shadow: inset 0 4px 0 rgba(255, 119, 102, 0.5);
}
.github-logo {
  width: auto;
  height: 20px;
  padding: 8px 8px;
}
.hdg_1 {
  font-size: 32px;
}
.hdg-heavy {
  font-weight: 700;
}
.mix-hdg_inline {
  display: inline;
}
#airportInfo {
  color: #dddddd;
  font-size: 12px;
  left: 0;
  padding: 8px;
  position: absolute;
  top: 0;
  z-index: 1000;
}
.airportInfo-label {
  width: 35px;
  float: left;
  padding-bottom: 4px;
}
.airportInfo-value {
  width: 110px;
  float: left;
  border-left: 1px solid;
  padding-left: 6px;
  padding-bottom: 4px;
  margin-left: 6px;
}
.loadingView {
  /* using absolute and 0s to force full screen cover when loading screen is active */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #0d1b1e;
  color: #dddddd;
  z-index: 1500;
}
.loadingViewMedia {
  width: 500px;
  overflow: hidden;
}
.loadingViewMedia-figure {
  float: left;
  margin-right: 50px;
}
.loadingViewMedia-content {
  margin-top: 10px;
  overflow: hidden;
}
.loadingViewMedia-content > * + * {
  margin-top: 5px;
}
.loadingViewMedia-hd {
  padding-bottom: 5px;
}
.loadingViewMedia-bd {
  color: #a3e3e8;
  font-size: 80%;
}
.loadingViewMedia-ft {
  padding-top: 10px;
  color: #888888;
  font-size: 70%;
}
.loadingView-dot {
  opacity: 0;
  animation: showHideDot 1s ease-in-out infinite;
}
.loadingView-dot_one {
  animation-delay: 0.1s;
}
.loadingView-dot_two {
  margin-left: -2px;
  animation-delay: 0.2s;
}
.loadingView-dot_three {
  margin-left: -2px;
  animation-delay: 0.3s;
}
@keyframes showHideDot {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
#log {
  width: calc(100% - 200px);
  position: absolute;
  left: 10px;
  bottom: 40px;
  font-size: 80%;
  color: #dddddd;
  pointer-events: none;
  z-index: 100;
}
#log .item {
  padding: 3px 5px;
  display: block;
  color: #dddddd;
  opacity: 0.8;
  transition: opacity 10s ease;
}
#log .item.warn {
  color: #ff7766;
  opacity: 1;
}
#log .item.hidden {
  opacity: 0;
}
#log .from {
  display: inline-block;
}
#log .from:after {
  margin-right: 0.4em;
  display: inline-block;
  content: ":";
}
.media {
  overflow: hidden;
}
.media-figure {
  float: left;
  margin-right: 20px;
}
.media-content {
  overflow: hidden;
}
.media-content > * + * {
  margin-top: 10px;
}
#paused {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  background: rgba(0, 0, 0, 0.5);
  color: #ddd;
  opacity: 0;
  pointer-events: none;
  z-index: 500;
}
#paused img {
  margin-left: -32px;
  margin-top: -32px;
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0.5;
}
#paused img:hover {
  opacity: 0.7;
  cursor: pointer;
}
#paused img:active {
  opacity: 0.4;
  cursor: pointer;
}
html.paused #paused {
  opacity: 1;
  pointer-events: auto;
}
/* TODO: text colors should be moved to [future] _txt.less rules */
#score {
  padding: 8px 20px 8px 10px;
  color: rgba(255, 255, 255, 0.5);
  flex-basis: 135px;
  text-align: right;
}
#score.negative {
  color: rgba(255, 64, 64, 0.8);
}
.dialog {
  display: flex;
  flex-direction: column;
  height: auto;
  max-height: calc(100% - 2*36px - 2*7px);
  width: 600px;
  margin: auto;
  padding: 0px 10px 10px 10px;
  position: absolute;
  top: 36px;
  left: 0;
  right: 0;
  background-color: rgba(20, 20, 20, 0.6);
  color: #dddddd;
  font-size: 12px;
  border: 6px solid #28505a;
  border-radius: 8px;
  box-sizing: border-box;
  box-shadow: 0 0 50px -10px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
  z-index: 200;
}
.dialog select {
  width: 100%;
  padding-right: 2.5em;
  /* accommodate with the pseudo elements for the dropdown arrow */
  background-color: #141414;
  color: #dddddd;
  font-family: inherit;
  line-height: 25px;
  border: 0;
  border-radius: 3px;
  appearance: none;
  cursor: pointer;
}
.dialog select:focus {
  outline: none;
}
.dialog-body {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 5px 5px;
  background-color: #141414;
  color: #dddddd;
  font-size: 100%;
  border: 2px solid #28505a;
  border-radius: 10px;
  overflow-y: auto;
}
.dialog-footer {
  color: #666666;
  display: inline-block;
  font-style: italic;
  margin-top: 10px;
  text-align: center;
  width: 100%;
}
.dialog.open {
  opacity: 1;
  pointer-events: auto;
}
.dialog-title {
  padding: 10px 0 7px 0;
  font-size: 170%;
  text-align: center;
}
.form-element {
  display: flex;
}
.form-element input[type="range"] {
  display: block;
  flex-grow: 1;
}
.form-element + .form-element {
  margin-top: 2px;
}
.form-label {
  display: block;
  flex-grow: 1;
  box-sizing: border-box;
  width: 65%;
  padding-right: 10px;
  line-height: 25px;
}
.form-type-select {
  position: relative;
  width: 35%;
  display: inline-block;
  color: inherit;
}
.form-type-select::after {
  position: absolute;
  right: 0;
  font-size: 8px;
  line-height: 25px;
  content: "\25BC";
  pointer-events: none;
}
.form-value {
  line-height: 25px;
  padding: 0px 5px;
  width: 30px;
  overflow: hidden;
}
input[type=number].form-input {
  /* ensure number steppers are visible */
  min-width: 50px;
}
